.registerContainer {
  height: 100%;
  width: 100%;
  max-width: 50vh;
  margin: 10vh 5vh 5vh;
  padding: 10vh 0 5vh;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  background: none;
}

.registerTitle {
  font-size: 24px;
  color: black;
  font-weight: 700;
  margin-bottom: 20px;
}

.registerForm {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.formGroup {
  text-align: left;
}

input {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: none;
  border-radius: 0;
  border-bottom: 2px solid black;
  outline: none;
  box-sizing: border-box;
  background: none;
}

input:focus {
  border-color: #b9dbff;
}

.errorMessage {
  font-size: 14px;
  color: #fd4b4b;
  font-weight: 600;
  margin-top: 5px;
}

.registerButton {
  background: linear-gradient(to right, #5e7d85, #9a8e88);
  color: white;
  font-weight: 600;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.registerButton:hover {
  background-color: #0056b3;
}